/**
 * Styles for the header block.
 */
.header {
  background: #212429;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 999;
  display: flex;
  justify-content: space-between;
}

.header input {
  display: none;
}

.header label {
  cursor: pointer;
}

.header .nav {
  user-select: none;
  z-index: 999;
}

.header .nav__showmenu {
  position: relative;
  z-index: 997;
  display: inline-block;
  margin-left: 0px;
  padding: 8px 20px 2px 19px;
  border-left: 1px solid #3c3c3c;
  border-right: 1px solid #3c3c3c;
  top: 2px;
  font-size: 23px;
  color: #fff;
  margin-bottom: 0px;
  bottom: 0px;
}

.header .nav__back {
  position: fixed;
  top: 0;
  right: 10px;
  transition: background-color .3s;
}

.header .menu, .header .menu__submenu {
  list-style: none;
  padding: 0;
  color: #fff;
  overflow: hidden;
}

.header .menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 220px;
  margin: 0;
  padding: 17px 0;
  text-transform: uppercase;
  line-height: 2em;
  background-color: #2c2c2c;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: translateX(-100%);
  transition: transform .3s;
  will-change: transform;
  backface-visibility: hidden;
  z-index: 998;
}

.header .menu__submenu {
  font-size: .8em;
  background-color: #ddd;
  max-height: 0;
  transition: max-height .3s ease-in-out;
  color: #fff;
}

.header .menu__showsub {
  position: absolute;
  right: 0;
  margin-top: -34px;
  padding: 10px;
  line-height: normal;
}

.header .menu__item {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #fff;
}

.header .menu__submenu .menu__item {
  color: #000;
}

.header .nav__check:checked ~ .nav__back {
  background-color: rgba(0, 0, 0, 0.2);
  bottom: 0;
}

.header .menu__item:hover, .header .nav__showmenu:hover, .header .menu__showsub:hover {
  opacity: 0.7;
}

.header .menu::-webkit-scrollbar {
  display: none;
}

.header .nav__check:checked + .nav__showmenu {
  color: #fff;
}

.header .menu__check:checked ~ .menu__submenu {
  max-height: 100vh;
}

.header .menu__check:checked + .menu__showsub {
  transform: rotate(180deg);
}

.header .nav__check:checked ~ .menu {
  transform: translateX(0);
}

