  .recomposts .next-arrow {
    display: none;
  }
}

.recomposts .prev-arrow {
  position: absolute;
  top: 2%;
  right: 80px;
  background: #000;
  padding: 10px;
  transform: rotate(90deg);
}

@media (max-width: 768px) {
  .recomposts .prev-arrow {
    display: none;
  }
}

